import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { useHistory, useParams, useLocation } from 'react-router-dom';

import SubBody from './component/SubBody'

const SubPage = () => {

  const location = useLocation()
  const history = useHistory()

  const [isName, setIsName] = useState('');

  const resName = history.location.state;
  //    

  localStorage.setItem('id', JSON.stringify(resName));
  //  localStorage id  

  useEffect(() => {

    const saveName = localStorage.getItem('id');
    // localStorage   

    if (saveName !== null) {
      setIsName(saveName.substring(1, saveName.length-1))
      //  null   state 
    }
    
  }, [resName])

  return (
    <Container>
      <Wrap>
        <P><span>{isName}</span> .</P>
        
        <SubBody />
      </Wrap>
    </Container>
  );
};

export default SubPage;

const Container = styled.div`
  width: 100%;
`;

const Wrap = styled.div`
  /* width: 100%; */
  padding: 20px;
`;

const P = styled.p`
  display: flex; justify-content: flex-end; align-items: center;
  font-size: 20px; color: #5182E2; font-weight: bold;
  width: 100%;

  & span {
    font-size: 25px; font-weight: bold; color: #363F8F;
  }
`;
